function drawHelp1(c, fx, fy, tx, ty, n) {
  var a, hl = 15, s, x, y, q = 0.2, l;
  if (!tx || !ty) {
    return;
  }
  tx = tx();
  ty = ty();
  if (!tx || !ty) {
    return;
  }
  a = Math.atan2(ty-fy, tx-fx);
  c.beginPath();
  if (dhlplf === 1) {
    c.setLineDash([0,4,0]);
  } else {
    c.setLineDash([2,0,2]);
  }
  c.lineWidth = 1;
  c.strokeStyle = "rgba(140, 140, 180, 0.7)";
  c.moveTo(fx, fy);
  c.lineTo(tx, ty);
  c.stroke();
  c.beginPath();
  c.moveTo(tx, ty);
  c.setLineDash([]);
  c.lineWidth = 3;
  c.lineTo(tx-hl*Math.cos(a-Math.PI/6),ty-hl*Math.sin(a-Math.PI/6));
  c.stroke();
  c.beginPath();
  c.moveTo(tx, ty);
  c.setLineDash([]);
  c.lineWidth = 3;
  c.lineTo(tx-hl*Math.cos(a+Math.PI/6),ty-hl*Math.sin(a+Math.PI/6));
  c.stroke();
  s = "(" + n +")";
  l = s.length * 3;
  x = fx + (tx - fx) * q - l;
  y = fy - (fy - ty) * q + l;
  c.font = "12px " + font;
  c.fillStyle = "rgba(220, 220, 220, 0.9)";
  c.fillText(s, x, y);
}

function strokerr(c, x, y, w, h, r) {
  c.beginPath();
  c.moveTo(x+r, y);
  c.lineTo(x+w-r, y);
  c.quadraticCurveTo(x+w, y, x+w, y+r);
  c.lineTo(x+w, y+h-r);
  c.quadraticCurveTo(x+w, y+h, x+w-r, y+h);
  c.lineTo(x+r, y+h);
  c.quadraticCurveTo(x, y+h, x, y+h-r);
  c.lineTo(x, y+r);
  c.quadraticCurveTo(x, y, x+r, y);
  c.stroke();
}

function fillrr(c, x, y, w, h, r) {
  c.beginPath();
  c.moveTo(x+r, y);
  c.lineTo(x+w-r, y);
  c.quadraticCurveTo(x+w, y, x+w, y+r);
  c.lineTo(x+w, y+h-r);
  c.quadraticCurveTo(x+w, y+h, x+w-r, y+h);
  c.lineTo(x+r, y+h);
  c.quadraticCurveTo(x, y+h, x, y+h-r);
  c.lineTo(x, y+r);
  c.quadraticCurveTo(x, y, x+r, y);
  c.fill();
}

