function dspmsg(m, d, f) {
  var i, j, tm;
  if (!dmsgs || !f && m[0] == msgl1 && m[1] == msgl2 && m[2] == msgl3) {
    drawMsgsStm(m[0], m[2]);
    return;
  }
  msgl1 = m[0];
  msgl2 = m[1];
  msgl3 = m[2];
  i = 0;
  j = 0;
  while (i < msgll) {
    while (i < msgll && msgl[i][0]) {
      i++;
    }
    if (i < msgll) {
      if (j < i) {
        j = i + 1;
      }
      while (j < msgll && !msgl[j][0]) {
        j++;
      }
      if (j < msgll) {
        msgl[i][0] = msgl[j][0];
        msgl[i][1] = msgl[j][1];
        msgl[i][2] = msgl[j][2];
        msgl[i][3] = msgl[j][3];
        msgl[i][4] = msgl[j][4];
        msgl[j][0] = null;
        msgl[j][4] = null;
      }
    }
    while (msgll > 0 && !msgl[msgll-1][0]) {
      msgll--;
    }
  }
  tm = [];
  for (i = 0; i < m.length; i++) {
    tm.push(ctx2.measureText(m[i]));
  }
  if (msgll < msgl.length) {
    msgl[msgll][0] = m;
    msgl[msgll][1] = d?d:3500;
    msgl[msgll][2] = performance.now();
    msgl[msgll][3] = 0;
    msgl[msgll][4] = tm;
    msgll++;
  } else {
    msgl[msgll++] = [m,d?d:3500,performance.now(),0,tm];
  }
}

function hbckhac() {
  var i, k, h, hb = [];
  if (cellsn === 0) {
    return 0;
  }
  for (i = 0; i < 256; i++) {
    hb[i] = 0;
  }
  for (i = 0; i < cellslen; i++) {
    bmemgA(i);
    if (bmemv3 === 1) {
      k = crkey(bmemv0-cellsl,bmemv1-cellsb);
      h = 0;
      h = ((h<<5)-h) + (k & 0xFF);
      h = h & h;
      h = ((h<<5)-h) + ((k>>>8) & 0xFF);
      h = h & h;
      h = ((h<<5)-h) + ((k>>>16) & 0xFF);
      h = h & h;
      h = ((h<<5)-h) + ((k>>>24) & 0xFF);
      h = h & h;
      hb[h & 0xFF] += ((h>>>24) & 0xF);
      hb[(h>>>8) & 0xFF] += ((h>>>16) & 0xF0);
      hb[(h>>>16) & 0xFF] += ((h>>>8) & 0xF);
      hb[(h>>>24) & 0xFF] += (h & 0xF0);
    }
  }
  h = 0;
  for (i = 0; i < hb.length; i++) {
    k = hb[i];
    h = ((h<<5)-h) + (k & 0xFF);
    h = h & h;
    h = ((2<<5)-h) + ((k>>>8) & 0xFF);
    h = h & h;
    h = ((h<<5)-h) + ((k>>>16) & 0xFF);
    h = h & h;
    h = ((h<<5)-h) + ((k>>>24) & 0xFF);
    h = h & h;
  }
  return h;
}

function hbckcheck() {
  var p, i, h, x, n, hi, q, j, c, r, g, z, k, p0, q0, q1, pp, dx, dy, pn, px;
  if (!hbcksl || gennum < 2 || hbcksll < 3) {
    return;
  }
  p = 0;
  h = hbcksl[hbckslf];
  i = hbckslf;
  while (p < 2 || p <= gennum && p < hbcksll && h !== hbcksl[i]) {
    p++;
    i++;
    if (i >= hbcksl.length) {
      i = 0;
    }
  }
  if (p <= gennum && p < hbcksll) {
    x = new Map();
    for (i = 0; i < cellslen; i++) {
      bmemgA(i);
      if (bmemv3 === 1) {
        x.set(crkey(bmemv0,bmemv1), true);
      }
    }
    p0 = 0;
    n = cellsn;
    q = [histh,histi];
    rclHist0_1();
    q0 = prevHist(q[0], q[1], 2);
    pn = 0;
    px = hist[histh][0][histi][14];
    for (j = 1; j <= p; j++) {
      if (p0 > 0) {
        rclHist0_2();
        q1 = rlHist(q[0], q[1], p0--);
        rclHist1_2();
        hi = hist[q1[0]][0][q1[1]];
        if (p0 === 0 && j < p) {
          q0 = prevHist(q1[0], q1[1], 2);
        }
      } else {
        q = q0;
        hi = hist[q[0]][0][q[1]];
        if (j < p) {
          q0 = prevHist(q[0], q[1], 2);
          if ((pp = hist[q0[0]][0][q0[1]][11][0]) > 0) {
            p0 = hist[q0[0]][0][q0[1]][11][1];
            dx = hist[q0[0]][0][q0[1]][11][2];
            dy = hist[q0[0]][0][q0[1]][11][3];
          }
        }
      }
      pn += px - hi[14];
      px = hi[14];
      i = hi[0].length - 1;
      while (i >= 0) {
        if (hi[0][i][3] !== 1) {
          c = hi[0][i][0] + (p0===0||pp===0?0:Math.ceil(p0/pp)*dx);
          r = hi[0][i][1] + (p0===0||pp===0?0:Math.ceil(p0/pp)*dy);
          if (c < maxl) {
            c = maxr - maxl + c + 1;
          }
          if (c > maxr) {
            c = maxl + c - maxr - 1;
          }
          if (r < maxb) {
            r = maxt - maxb + r + 1;
          }
          if (r > maxt) {
            r = maxb + r - maxt - 1;
          }
          g = hi[0][i][2];
          k = crkey(c,r);
          z = x.get(k);
          if (g >= 0) {
            if (!z) {
              x.set(k, true);
              n++;
            }
          } else if (g === -1) {
            if (z) {
              x.delete(k);
              n--;
            }
          }
        }
        i--;
      }
    }
    rclHist1_1();
    if (n === cellsn) {
      dx = cellsl - hi[9];
      dy = cellsb - hi[8];
      i = 0;
      while (i < cellslen) {
        bmemgA(i);
        if (bmemv3 === 1 && !x.get(crkey(bmemv0-dx,bmemv1-dy))) {
          break;
        }
        i++;
      }
      if (i < cellslen) {
        n = -1;
      }
    } else {
      n = -1;
    }
    if (n === -1) {
      drawMsgsStm("\\  \\  \\  \\  \\", "/  /  /  /  /"); // false positive
      msgf1 = 911;
    } else {
      clrcellsx();
      hist[histh][0][histi][11][0] = p;
      hist[histh][0][histi][11][1] = 0;
      hist[histh][0][histi][11][2] = dx;
      hist[histh][0][histi][11][3] = dy;
      hist[histh][0][histi][16] = pn;
      if (dx === 0 && dy === 0) {
        dspmsg(["Oscillator!   ","\u22C5      \u22C5","p"+p+" "], 4500, true);
        msgf1 = 21;
      } else {
        dspmsg(["Spaceship!        ","\u22C5      \u22C5",spcshtxt(p,dx,dy)+"    "], 4500, true);
        msgf1 = 22;
      }
      histp = 1;
      if (run && stops) {
        swrun();
        stopscd = performance.now();
      }
    }
  }
}

function spcshtxt(p, x, y) {
  var k, x0, y0, p0, n;
  x0 = Math.abs(x);
  y0 = Math.abs(y);
  k = Math.max(x0, y0);
  p0 = p;
  n = Math.min(k, p0);
  while (n > 1) {
    if (k / n === Math.floor(k / n) && p0 / n === Math.floor(p0 / n)) {
      break;
    }
    n--;
  }
  return ((k/n)>1?k/n:"")+"c/"+(p0/n)+" p"+p0+(x===0?"":" "+(x<0?"\u21E0":"\u21E2")+x0)+(y===0?"":(x===0?" ":"")+(y<0?"\u21E3":"\u21E1")+y0);
}

